@import "variables";
@import "mixins";

body {
  font-family: $font-family;
  background-color: $background-white;
}

.container {
  @include padding-margin(0 15px, 0 auto);
  @include width-value(100%);
  max-width: $content-width;
}


@media (max-width: 1400px) {
  .container {
    max-width: 1171px;
  }
}

@media (max-width: 1200px) {
  .container {
    max-width: 970px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 750px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 538px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 280px;
  }
}